  }
}
.title-container{
  text-align: center;
  margin-bottom: 30px;
  background: linear-gradient(rgba(25, 25, 25, 0.6), rgba(25, 25, 25, 0.6)), url("title-bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  padding:60px 0;
  color: white;
}
.title-container h1{
  font-size: 60px;
  font-weight: bold;
  margin-bottom: 20px;
}