.why-container{
    background-color: white;
    margin-top: 100px;
    margin-bottom: 20px;
}
.why-container h1{
    font-size: 45px;
    text-align: center;
    padding: 20px 0;
}
.why-content{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.why-left img{
    max-width:600px;
}
.why-right h2{
    font-size: 45px;
}
.why-right{
    max-width: 700px;
    padding: 10px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
}
.why-right p{
    max-width: 700px;
}
.vision-mission{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding: 90px 0;
    cursor: pointer;
}
.benefits{
    margin-top: -70px;
}
@media(max-width:789px){
    .benefits{
        margin-top: 0;
    }
}
.awards{
    display: flex;
    justify-content: start;
    padding: 20px 0;
    margin-left: 5.5%;
}
.list {
    position: relative;
    align-self: center;
  }
  .list h2 {
    font-weight: 700;
    letter-spacing: 1px;
    margin-bottom: 10px;
  }
  .list ul {
    position: relative;
  }
  .list ul li {
    position: relative;
    left: 0;
    color:black;
    list-style: none;
    margin: 4px 0;
    border-left: 2px solid rgb(0,65,100);
    transition: 0.5s;
    cursor: pointer;
  }
  .list ul li:hover {
    transition: 0.3s ease;
    left: 5px;
    color: white;
  }
  .list ul li span {
    position: relative;
    padding: 8px;
    padding-left: 12px;
    display: inline-block;
    z-index: 1;
    transition: 0.5s;
    text-align: left;
  }
  .list ul li:hover span {
    color: white;
  }
  .list ul li:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgb(0,65,100);
    color: white;
    transform: scaleX(0);
    transform-origin: left;
    transition: 0.5s;
  }
  .list ul li:hover:before {
    transform: scaleX(1);
  }

@media (max-width: 768px) {
    .why-left img{
        max-width:320px;
    }
    .list {
        position: relative;
        width: 90%;
        align-self: center;
      }
}